import Vuex from 'vuex'
import Vue from 'vue'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    width: 200,
    collapse: false,
    // subRouter: ""
    select: '',
    type: '',
    subtype: ''
  },
  mutations: {
    close (state) {
      state.width = 64
      state.collapse = true
    },
    open (state) {
      state.width = 200
      state.collapse = false
    },
    select (state, val) {
      state.select = val.select
      state.type = val.type
      state.subtype = val.subtype
    }
  },
  actions: {
    turnOff ({ commit }) {
      commit('close')
    },
    turnOn ({ commit }) {
      commit('open')
    },
    select ({commit}, val) {
      commit('select', val)
    }
  }
})

export default store



