import Vue from 'vue'
import Router from 'vue-router'
// eslint-disable-next-line
import axios from 'axios'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('../components/Login')

    },
    {
      path: '/admin',
      name: 'admin',
      // beforeEnter: (to, from, next) => {
      //   axios.get('http://localhost:8030/api/checktoken')
      //     .then(res => {
      //       if (res.data.code === 0) {
      //         next()
      //       } else {
      //         next('/login')
      //       }
      //     })
      // },
      component: () => import('../components/Admin'),
      children: [
        {path: '/', redirect: 'home'},
        {path: 'home', component: () => import('../components/components/home.vue')},
        {
          path: 'exam/:id',
          component: () => import('../components/components/exam.vue'),
          beforeEnter: (to, from, next) => {
            console.log(to, from)
            // TODO 在刷新的时候根据路由初始化vuex数据
            next()
          }
        }
        // {path: '*', redirect: 'home'}
      ]
    }
  ]
})



